.message {
    display: flex;
    align-items: flex-end;


}

.message_content {
    background-color: teal;
    width: 180px;
    height: 100px;
    border-radius: 30px 30px 30px 0px;
}


.message_avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}


.message_name {
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    color: violet;
}

.message_time {
    font-size: 10px;
    justify-content: flex-end;
    color: whitesmoke;
    padding: 5px 135px;
}

.message_text {
    font-size: 15px;
    text-align: center;
    color: white;
    padding: 20px 10px
}

.corner {
    width: 5px;
    height: 5px;
    background: radial-gradient(circle at 0 0, rgba(0, 0, 0, 0) 10px, rgba(255, 255, 255, 0.3) 0);
}


