.container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.dark {
    background: dimgrey;
}

.dark-text {
    color: aliceblue;
    text-shadow: 1px 1px 1px #000
}

.light {
    background: aliceblue;
}

.light-text {
    color: black;
    text-shadow: 1px 1px 1px #000
}

.red {
    background: lightcoral;
}

.red-text {
    color: aqua;
    text-shadow: 1px 1px 1px #ff0000
}

.yellow {
    background: yellow;
}

.yellow-text {
    color: #0009ff;
    text-shadow: 1px 1px 1px #07e8f3
}

.green {
    background: lightgreen;
}

.green-text {
    color: #060a79;
    text-shadow: 1px 1px 1px #ff0000
}

.gold {
    background: gold;
}

.gold-text {
    color: darkcyan;
    text-shadow: 1px 1px 1px #69d383
}

.texture {
    background-image: url('data:image/jpeg;base64,') ;

    color:cornsilk;
}

.default {
    background-image: url('data:image/jpeg;base64,');
}